/* el-messiri-arabic-500-normal*/
@font-face {
  font-family: 'El Messiri';
  font-style: normal;
  font-display: swap;
  font-weight: 500;
  src: url('./files/el-messiri-arabic-500-normal.woff2') format('woff2'), url('./files/el-messiri-all-500-normal.woff') format('woff');
  unicode-range: U+0600-06FF,U+200C-200E,U+2010-2011,U+204F,U+2E41,U+FB50-FDFF,U+FE80-FEFC;
}
